header {
  width: 100%;
  background: #222;

  .inner {
    width: 1180px;
    height: 80px;
    margin: 0px auto;
    position: relative;

    h1 {
      position: absolute;
      bottom: 20px;
      left: 0px;

      a {
        font: bold 24px/1 "arial";
        color: #fff;
      }
    }

    #gnb {
      position: absolute;
      bottom: 0px;
      right: 0px;

      li {
        float: left;

        a {
          display: inline-block;
          padding: 20px 30px;
          font: bold 14px/1 "arial";
          color: #fff;
        }
      }
    }

    .fa-bars {
      position: absolute;
      right: 30px;
      bottom: 20px;
      font-size: 24px;
      color: #fff;
      display: none;
    }
  }

  &.main {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    background: transparent;
  }
}

@media screen and (max-width: $tablet) {
  header {
    .inner {
      width: 100%;

      h1 {
        left: 30px;
      }
      #gnb {
        display: none;
      }
      .fa-bars {
        display: block;
      }
    }
  }
}
