.content {
  figure {
    width: 100%;
    height: 300px;
    background: gray;
  }

  .inner {
    width: 1180px;
    min-height: 400px;
    margin: 100px auto;
    transform: translateY(100px);
    opacity: 0;
    transition: 1s;
  }

  &.on {
    .inner {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

//table
@media screen and (max-width: $tablet) {
  .content {
    figure {
      height: 200px;
    }

    .inner {
      width: 100%;
      margin: 80px auto;
      padding: 0 30px;
    }
  }
}
